html,
body
	width: 100%
	height: 100%
	overflow: hidden
	background-color: black
	margin: 0px
	padding: 0px

body
	display: grid

#display
	width: 100%
	height: 100%
	overflow: hidden

.hint
	margin: 30px 0px 0px 30px
	position: absolute
	padding: 30px
	background-color: white
	font-size: larger
	white-space: pre

.error
	width: 100%
	height: 100%
	background-color: white
	display: flex
	flex-direction: column
	justify-content: center
	align-content: center
	align-items: center

	&>.large
		color: darkred
		font-size: 100px

	&>.normal
		color: black
		font-size: 30px
